  height: 500px;
  overflow: hidden;
}

@media (max-width: 768px) {
  .post_page {
    grid-template-columns: 1fr;
    height: 100%;
  }
}

.post_page .post_details {
  display: flex;
  flex-direction: column;
}

.post_page .img_div {
  height: 500px;
  background-position: center;
  background-size: cover;
}

.post_page .post_details header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  border-bottom: 1px solid rgb(207, 207, 207);
}

.post_page .post_details header a {
  margin-left: 10px;
}

.post_page .post_details header > div {
  display: flex;
  align-items: center;
}

.post_details .body {
  flex: 10 1;
  overflow-y: scroll;
  display: flex;
}

.post_page .post_details img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.post_page .post_details a {
  color: #000;
  text-decoration: none;
  display: inline-block;
  font-weight: 600;
}

.post_page .post_details a:hover {
  text-decoration: underline;
}

.post_details section {
  padding: 1rem;
}

.post_details section > div {
  display: grid;
  grid-template-columns: 10% 90%;
}

.post_details .pb {
  padding-left: 10px;
  display: flex;
  height: 100%;
  max-height: 300px;
  flex-direction: column;
}

.post_details .pb p {
  font-size: 14px;
}

.post_details .pb a {
  margin-left: 0;
}

.post_details .footer {
  background: rgb(206, 206, 206);
  border-top: 1px solid rgb(206, 206, 206);
}

.post_details .footer .icons {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}

.post_details .footer #icon {
  font-size: 1.6rem;
  cursor: pointer;
  margin: 0 10px;
}

.post_details .footer #icon:first-child {
  margin-left: 0;
}
.post_details .footer #icon:nth-child(2) {
  margin-top: -3px;
}

.dropdown_menu {
  width: 400px;
  background-color: #fff;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  overflow: hidden;
}

.dropdown_menu .drop_down_items button {
  display: block;
  width: 100%;
  height: 45px;
  outline: 0;
  cursor: pointer;
  border: 0;
  border-bottom: 1px solid #c4c4c4;
}

.dropdown_menu .drop_down_items button:last-child {
  border-bottom: 0;
}

.dropdown_menu .drop_down_items button:hover {
  background: #fff;
}

.main_loader {
  position: absolute;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background: rgba(0, 0, 0, 0.1);
  color: #fff;
  font-size: 3rem;
}

:root {
  --primary-color: rgb(0, 149, 246);
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background-color: #f0f0f0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.main_container {
  max-width: 992px;
  margin: 0 auto;
  padding: 0 1rem;
}

select {
  width: 100%;
  outline: 0;
  padding: 5px;
  border: 2px solid rgb(212, 212, 212);
  border-radius: 4px;
  cursor: pointer;
}

.link {
  font-weight: 600;
  text-decoration: none;
  color: #000;
}

.main_overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 999;
}

.custom_link {
  color: rgb(0, 149, 246);
  cursor: pointer;
  font-weight: 600;
  font-size: 14px;
}

.btn {
  padding: 5px 1rem;
  background-color: rgb(33, 130, 240);
  border: 0;
  outline: 0;
  display: inline-block;
  margin-top: 10px;
  color: #fff;
  border-radius: 3px;
  cursor: pointer;
}

.btn.loading_btn {
  min-width: 100px;
}

.btn:hover {
  opacity: 0.8;
}

.success_msg {
  color: #f2f2f2;
  font-weight: 400;
  letter-spacing: 1px;
  padding: 8px 10px;
  background: #49e849;
  border-radius: 3px;
  display: flex;
  justify-content: space-between;
}

.error_msg {
  color: #f2f2f2;
  font-weight: 400;
  letter-spacing: 1px;
  padding: 8px 10px;
  background: #d33d10;
  border-radius: 3px;
  display: flex;
  justify-content: space-between;
}

.success_msg span {
  cursor: pointer;
}

/* medica queries */
@media (max-width: 768px) {
  .home_sidebar {
    display: none;
  }

  #side_bar_container {
    display: none;
  }
}

